  .sgnm-sewa-widow-sewa-container img {
    width: 100%; }
    @media only screen and (min-width: 768px) {
      .sgnm-sewa-widow-sewa-container img {
        width: 80%; } }

.sgnm-sewa-widow-sewa-container {
  position: relative; }

.sgnm-sewa-info-container {
  text-align: center;
  -webkit-box-shadow: 0 2px 6px 0px #777;
  -moz-box-shadow: 0 2px 6px 0px #777;
  box-shadow: 0 2px 6px 0px #777;
  padding: 2rem;
  background: #fff; }
  @media only screen and (min-width: 768px) {
    .sgnm-sewa-info-container {
      width: 50%;
      position: absolute;
      right: 0;
      top: 8rem; } }

.contact-us-address-container {
  width: 50%;
  margin: 5rem;
  color: #7f7474; }
  .contact-us-address-container h2 {
    color: #CE3203;
    text-transform: uppercase; }
  .contact-us-address-container div {
    margin: 0 0 1rem 1rem; }

.contact-us-address-wrapper {
  border-bottom: 1px solid #E5E5E5; }

.register-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0; }
  .register-container iframe {
    display: block;
    width: 100%;
    height: 100%;
    border: none; }
