// Header
//    - Logo
//    - Navigation

// Header
.primary-header {
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 10;
}

/*******************************************
  * Primary Logo
  *****************************************/
  .primary-logo {
    width: 100%;
    max-width: 15rem;
    margin-top: 20px;
    margin-left: 10px;
    @media only screen and (min-width: $primary-navigation-breakpoint) {
      max-width: 17rem;
    }
  }
  /*** End Primary Logo ***/

/*******************************************
  * Primary Navigation
  *****************************************/
  .primary-header-navbar-toggle {
    border: 1px solid $color-white;
    padding: 9px 10px;
    margin: 8px 0;
    border-radius: 4px;
    background-color: transparent;
    float: right;
    margin-top: 62px;
    margin-right: 33px;

    @media only screen and (min-width: $primary-navigation-breakpoint) {
      display: none;
    }
  }

  .primary-header-navbar-toggle_icon_comp {
    background-color: $color-white;
    display: block;
    width: 22px;
    height: 2px;
    border-radius: 1px;
  }

  .primary-header-navbar-toggle_icon_comp+.primary-header-navbar-toggle_icon_comp {
    margin-top: 4px;
  }

  .primary-header-navbar_collapsed {
    display: none;
  }

  .primary-navigation {
    min-height: 50px;
    display: none;

    @media only screen and (min-width: $primary-navigation-breakpoint) {
      display: inline-block;
    }
  }

  .primary-navigation-list-container {
    padding: 0;
    margin: 0;
    background: rgba(0, 0, 0, 0.9);

    @media only screen and (min-width: $primary-navigation-breakpoint) {
      position: absolute;
      right: 40px;
      top: 70px;
      background: none;
    }

    li {
      text-align: center;
      border: 1px solid $color-white;
      margin: 0;

      @media only screen and (min-width: $primary-navigation-breakpoint) {
        margin-left: 1.7rem;
        display: inline-block;
        border: none;
      }

      a {
        font-size: 1.8rem;
        color: $color-white;
        width: 100%;
        height: 100%;
        display: inline-block;
        padding: 1.6rem .8rem;
      }

      a:hover, a.selected {
        @media only screen and (min-width: $primary-navigation-breakpoint) {
          border-bottom: 5px solid $color-orange;
        }
      }
    }
  }
  /*** End Primary Navigation ***/

/*******************************************
  * Primary Navigation with Background
  *****************************************/
  .header-bg {
    background-color: $color-black;
    width: 100%;
    height: 20rem;
  }

  .header-about {
    background: url('../images/about/header-bg-about.jpg') no-repeat;
    background-position: center center;
    background-size: cover;
  }

  .header-gallery {
    background: url('../images/gallery/header-bg-gallery.jpg') no-repeat;
    background-position: center center;
    background-size: cover;
  }

  .header-events {
    background: url('../images/events/header-bg-events.jpg') no-repeat;
    background-position: center center;
    background-size: cover;
  }

  .header-sewa {
    background: url('../images/sewa/header-bg-sewa.jpg') no-repeat;
    background-position: center center;
    background-size: cover;
  }

  .header-contact {
    background: url('../images/contact/header-bg-contact.jpg') no-repeat;
    background-position: center center;
    background-size: cover;
  }